<template>
  <div class="goodsShelves">
    <AppHeader>
      <p slot="center">货架列表</p>
    </AppHeader>

    <div style="height: 45px;">
      <div class="search-header-cont">
        <div class="search-header bg-fff">
          <van-search
            placeholder="请输入关键词搜索"
            shape="round"
            v-model="searchKey"
            @search="confirmSearch"
          />
        </div>
      </div>
    </div>

    <div class="goodsShelves-list" v-if="hasData">
      <router-link
        :to="{name: 'Top10'}"
        class="list-item"
        v-for="(items, index) in currentData"
        :key="index"
      >
        <div class="item-code">
          <p>{{ items.code }}</p>
        </div>
        <div class="item-img">
          <img :src=" baseURL + '/img/' + items.imagePath" />
        </div>
        <div class="item-operation">
          <router-link :to="{name: 'AddGoodsShelves'}" class="link">
            <p>编辑</p>
          </router-link>
        </div>
      </router-link>
    </div>

    <div class="pagination-module" v-if="hasData">
      <el-pagination
        background
        layout="prev, pager, next, jumper"
        :pager-count="5"
        :page-size="10"
        :total="recordcount"
        :current-page="currentPage"
        @current-change="pageCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

import ElementUI from "element-ui";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);
Vue.use(ElementUI);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";
import "element-ui/lib/theme-chalk/index.css";

export default {
  name: "GoodsShelves",
  components: {
    AppHeader
  },

  data() {
    return {
      data: {},
      searchKey: "",
      hasData: false,
      resultData: [],
      currentData: [],
      currentPage: 1,
      recordcount: "",

      returnRefresh: false
    };
  },
  mounted() {
    this.get_defaultData();
    this.get_data();
  },
  methods: {
    confirmSearch(val) {
      this.searchKey = val;
      this.get_data();
    },
    pageCurrentChange(val) {
      console.log(val);
      this.currentPage = val;
      this.currentData = this.resultData.slice(
        this.currentPage * 10 - 10,
        this.currentPage * 10
      );
      console.log(this.currentPage);
    },
    get_defaultData() {
      this.baseURL = this.$store.getters.getBaseURL;
      this.datetime = this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        storeId: this.getStoreInfo.gid,
        searchKey: this.searchKey
      };
      this.serverHost
        .fetchPost("/shelf/getRackTable", params)
        .then(res => {
          this.toast.clear();
          console.log(res);
          const resultData = res.data.aaData;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
            this.currentData = resultData.slice(
              this.currentPage * 10 - 10,
              this.currentPage * 10
            );
            this.recordcount = resultData.length;
          }

          console.log(this.currentData);
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

